body {
  background-color: #4d4f4e;
}

.header{
  color:white;
  background-color: black;
  text-align: left;
  margin-bottom: 7px;
}

.navbar {
  width: 10%;
  float: left;
  margin: 0;
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.link {
  text-decoration: none;
  display: block;
  color:white;
  background-color: black;
  margin: 3px 0;
  box-shadow: 3px 5px 50px #ffffff;
  padding: 3px;
}

.link:hover{
  color:white;
}

.greeting{
  background-color: black;
  color: white;
  padding: 10px;
  width: 80%;
  margin: 7px auto;
  box-shadow: 3px 5px 50px #ffffff;
  font-size: 0.7em;  
  border-radius: 5px;  
}
.greeting h2{
  font-size: 0.7rem;
}

/* everything related to charts */

.chartWrap{
  width: 80%;
  margin: 7px auto;
}
.chart-box{
  display:flex;
  flex-direction: row;    
}

.chart{
  margin-right:5px;
  box-shadow: 3px 5px 50px #ffffff;
  border-radius: 5px;
  
}
/* everything related to menu */
.menu{
  width:80%;
  margin:7px auto;
  background-color: #4d4f4e;
  color: white;
}
.restBar {
  margin:7px auto;
  background-color: black;
  box-shadow: 3px 5px 50px #ffffff;
  border-radius: 5px;
  color: white;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap; 
  padding: 7px;
}
.rest-logo {
  width: 50px;
  border-radius: 5px;
  margin-right: 3px;
}

.search{
  width: 100%;
  border-radius: 5px;
  background-color: black;
  color: white;

}

.tileFlexContainer {
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
}
.tile{
  font-size: 0.5em;
  margin: 0 5px 7px 0;
  background-color: black;
  box-shadow: 3px 5px 50px #ffffff;
  width: 190px;
}

.title {
  height: 3rem;
}

.tile-img{
  width: 150px;
  height: auto;
  margin:auto;
}

td {
  padding: 10px;
}

.tileForm p {
  margin-bottom: 3px;
}

.button{
  border-radius: 5px;
  background-color: black;
  color: white;
  border-color: white;
}

.hidden {
  display: none;
}

.tileFlexContainer {
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  align-items: flex-start;
  padding: 5px;
}

/* footer */
.footer{
  background-color: black;
  color: white;
  text-align: center;
  height: 80px; 
  font-size: 0.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Login page */

.loginDiv {
  background-color: black;
  color: white;
  padding: 10px;
  width: 80%;
  margin: 70px auto;
  box-shadow: 3px 5px 50px #ffffff;
  font-size: 0.7em;  
  border-radius: 5px;  
}

.loginInput {
  width: 100%;
  border-radius: 5px;
  background-color: #4d4f4e;
  color: white;
  margin-bottom: 20px;
}

.loginBtn {
  border-radius: 5px;
  background-color: black;
  color: white;
  border-color: white;
}

.selectInp {
  width: 100%;
  border-radius: 5px;
  background-color: black;
  color: white;
  margin-bottom: 2%;
}


